import * as React from 'react';
import { IconButton, ListItemIcon, Menu, MenuItem, Typography } from "@mui/material";
import { AppbarContainer, AppbarHeader } from "../../styles/appbar";
import MenuIcon from "@mui/icons-material/Menu";
import HomeIcon from "@mui/icons-material/Home";
import ProductIcon from "@mui/icons-material/Attachment";
import AboutIcon from "@mui/icons-material/Pets";
import Actions from "./actions";
import { Link } from 'react-router-dom';

export default function AppbarMobile({ matches }){
    const [anchorElNav, setAnchorElNav] = React.useState(null);
    const [anchorElUser, setAnchorElUser] = React.useState(null);

    const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
    };

    const handleCloseNavMenu = () => {
    setAnchorElNav(null);
    };

    return (
        <AppbarContainer>
            <IconButton onClick={handleOpenNavMenu}>
                <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
              }}
            >
                <MenuItem key={"Start"} component={Link} to="/" onClick={handleCloseNavMenu}>
                    <ListItemIcon>
                        <HomeIcon fontSize="small" />
                    </ListItemIcon>
                    <Typography textAlign="center">Start</Typography>
                </MenuItem>
                <MenuItem key={"Produkte"} component={Link} to="/produkte" onClick={handleCloseNavMenu}>
                    <ListItemIcon>
                        <ProductIcon fontSize="small" />
                    </ListItemIcon>
                    <Typography textAlign="center">Produkte</Typography>
                </MenuItem>
                <MenuItem key={"Ueberuns"} component={Link} to="/ueberuns" onClick={handleCloseNavMenu}>
                    <ListItemIcon>
                        <AboutIcon fontSize="small" />
                    </ListItemIcon>
                    <Typography textAlign="center">Über uns</Typography>
                </MenuItem>

            </Menu>
            <AppbarHeader textAlign={"center"} variant="h4">
                Benno onLein
            </AppbarHeader>
            <Actions matches={ matches } />
        </AppbarContainer>
    );
}